import UserPage from 'flarum/components/UserPage';
import listItems from "../../../../../core/js/src/common/helpers/listItems";

export default class TagsPage extends UserPage
{
    init() {
        super.init();

        this.show(app.session.user);
        app.setTitle(app.translator.trans('core.forum.settings.title'));
    }

    content() {
        return (
            <div className="SettingsPage">
                <ul>{listItems(this.settingsItems().toArray())}</ul>
            </div>
        );
    }

    show(user) {
        this.user = user;

        app.setTitle(user.displayName());

        m.redraw();
    }
}